@import '../../index.css';

.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	margin-top: 50px;
	align-items: center;
}

.titlebar-left {
	display: flex;
	flex-grow: 1;
	font-weight: 400px;
	font-size: 20px;
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
	color: #aaa;
}

.nav-item:hover {
	color: black;
}

.titlebar-center {
	flex-grow: 2;
	height: 100%;
}

.titlebar-right {
	flex-grow: 1;
	display: flex;
	font-size: 20px;
	justify-content: flex-end;
}

.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

.hamburger {
	display: none;
}

@media only screen and (max-width: 700px) {
	.titlebar-left, .nav-item {
		display: none;
	}

	.titlebar-center {
		flex-grow: 1;
	}

	.titlebar-right {
		justify-content: center;
	}

	.hamburger {
		display: block;
		width: 6vw;
		min-width: 30px;
	}

	.byline {
		display: none;
	}
}
