@import '../../index.css';

.grid {
	width: 100%;
	margin-top: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-flow: row wrap;
}

.grid-item {
	flex: 1 0 calc(100% / 3); 
	height: calc(100vw / 3);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.grid-img {
	width: 60%;
	height: 200px;
	max-width: 200px;
	max-height: 200px;
	min-width: 100px;
	min-height: 100px;

	margin-right: 10px;
	margin-left: 10px;
	margin-top: 10px;
}

.grid-text {
	visibility: hidden;
	margin-top: 20px;
	font-weight: 700;
	font-size: 16px;
	z-index: 1;
}

.grid-item:hover {
	opacity: 0.3;
	cursor: pointer;
}

.grid-item:hover .grid-text {
	visibility: visible;
	display: block;
	color: white;
	opacity: 1;
}

.skyscraper {
	background-color: var(--skyscraper);
}

.sudoku {
	background-color: var(--sudoku);
}

.stela {
	background-color: var(--stela);
}

.p2d {
	background-color: var(--p2d);
}

@media only screen and (max-width: 700px) {
	.grid {
		flex-direction: column;
	}

	.grid-item {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}
} 