@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700&display=swap);
:root {
  --skyscraper: #3189E6;
  --sudoku: #FD9A27; 
  --stela: #0C4473;
  --p2d: #94DDD9;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
   font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.titlebar-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-container {
  width: 100%;
}
.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	margin-top: 50px;
	align-items: center;
}

.titlebar-left {
	display: flex;
	flex-grow: 1;
	font-weight: 400px;
	font-size: 20px;
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
	color: #aaa;
}

.nav-item:hover {
	color: black;
}

.titlebar-center {
	flex-grow: 2;
	height: 100%;
}

.titlebar-right {
	flex-grow: 1;
	display: flex;
	font-size: 20px;
	justify-content: flex-end;
}

.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

.hamburger {
	display: none;
}

@media only screen and (max-width: 700px) {
	.titlebar-left, .nav-item {
		display: none;
	}

	.titlebar-center {
		flex-grow: 1;
	}

	.titlebar-right {
		justify-content: center;
	}

	.hamburger {
		display: block;
		width: 6vw;
		min-width: 30px;
	}

	.byline {
		display: none;
	}
}

.grid {
	width: 100%;
	margin-top: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-flow: row wrap;
}

.grid-item {
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: calc(100% / 3); 
	height: calc(100vw / 3);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.grid-img {
	width: 60%;
	height: 200px;
	max-width: 200px;
	max-height: 200px;
	min-width: 100px;
	min-height: 100px;

	margin-right: 10px;
	margin-left: 10px;
	margin-top: 10px;
}

.grid-text {
	visibility: hidden;
	margin-top: 20px;
	font-weight: 700;
	font-size: 16px;
	z-index: 1;
}

.grid-item:hover {
	opacity: 0.3;
	cursor: pointer;
}

.grid-item:hover .grid-text {
	visibility: visible;
	display: block;
	color: white;
	opacity: 1;
}

.skyscraper {
	background-color: var(--skyscraper);
}

.sudoku {
	background-color: var(--sudoku);
}

.stela {
	background-color: var(--stela);
}

.p2d {
	background-color: var(--p2d);
}

@media only screen and (max-width: 700px) {
	.grid {
		flex-direction: column;
	}

	.grid-item {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}
} 
